'use client';

import React from 'react';
import { motion, useReducedMotion } from 'framer-motion';
import Link from 'next/link';

import { Container } from '../container';
import { Button } from '../elements/button';

// Ha nálad máshol van, igazítsd az importot (de a lényeg: ez kezeli a Strapi relatív URL-t is)
import { StrapiImage } from '@/components/ui/strapi-image';

type ButtonTarget = '_self' | '_blank' | '_parent' | '_top' | string;

type CTAButton = {
  id?: number;
  text?: string | null;
  URL?: string | null;
  href?: string | null;
  variant?: string | null;
  target?: ButtonTarget | null;
  newTab?: boolean | null;
};

type StrapiMediaFormat = {
  url?: string;
  width?: number;
  height?: number;
};

type StrapiMedia = {
  url?: string;
  alternativeText?: string | null;
  caption?: string | null;
  width?: number;
  height?: number;
  formats?: Record<string, StrapiMediaFormat> | null;
} | null;

function resolveHref(locale: string, url?: string | null) {
  const u = (url || '').trim();
  if (!u) return `/${locale}`;

  // külső link / tel / mailto
  if (
    u.startsWith('http://') ||
    u.startsWith('https://') ||
    u.startsWith('mailto:') ||
    u.startsWith('tel:')
  )
    return u;

  // anchor a jelenlegi oldalon
  if (u.startsWith('#')) return u;

  const withSlash = u.startsWith('/') ? u : `/${u}`;
  // dupla // kiszedés
  return `/${locale}${withSlash}`.replace(/\/{2,}/g, '/');
}

function pickBestMediaUrl(media: StrapiMedia) {
  if (!media) return null;
  const f = media.formats || {};
  return (
    f?.large?.url ||
    f?.medium?.url ||
    f?.small?.url ||
    f?.thumbnail?.url ||
    media.url ||
    null
  );
}

export const CTA = ({
  heading,
  sub_heading,
  CTAs,
  cover,
  locale,
}: {
  heading?: string | null;
  sub_heading?: string | null;
  CTAs?: CTAButton[] | null;
  cover?: StrapiMedia;
  locale: string;
}) => {
  const reduce = useReducedMotion();

  const hasHeading = !!(heading && heading.trim().length);
  const hasSub = !!(sub_heading && sub_heading.trim().length);
  const hasButtons = !!(CTAs && CTAs.length);
  const coverSrc = pickBestMediaUrl(cover ?? null);
  const coverAlt =
    (cover?.alternativeText || cover?.caption || heading || 'Cover') ?? 'Cover';

  // ha tényleg semmi nincs, ne foglaljon helyet
  if (!hasHeading && !hasSub && !hasButtons && !coverSrc) return null;

  return (
    <section className="relative mx-4 rounded-xl bg-white py-10 overflow-hidden">
      <Container className="flex w-full flex-col items-center justify-between gap-10 px-8 md:flex-row md:gap-12">
        {/* LEFT: szöveg + gombok */}
        <div className="flex w-full flex-col md:max-w-[560px]">
          {hasHeading ? (
            <motion.h2
              initial={reduce ? false : { opacity: 0, y: 10 }}
              whileInView={reduce ? undefined : { opacity: 1, y: 0 }}
              viewport={{ once: true, amount: 0.5 }}
              transition={{ duration: 0.5, ease: [0.22, 1, 0.36, 1] }}
              className="mx-auto max-w-xl text-center text-xl font-bold text-lightblack md:mx-0 md:text-left md:text-3xl"
            >
              {heading}
            </motion.h2>
          ) : null}

          {hasSub ? (
            <motion.p
              initial={reduce ? false : { opacity: 0, y: 8 }}
              whileInView={reduce ? undefined : { opacity: 1, y: 0 }}
              viewport={{ once: true, amount: 0.5 }}
              transition={{
                duration: 0.5,
                delay: 0.06,
                ease: [0.22, 1, 0.36, 1],
              }}
              className="mx-auto mt-6 max-w-md text-center text-sm text-secondary md:mx-0 md:mt-8 md:text-left md:text-base"
            >
              {sub_heading}
            </motion.p>
          ) : null}

          {hasButtons ? (
            <div className="mt-10 flex flex-col gap-3 sm:flex-row sm:flex-wrap">
              {CTAs!.map((cta, index) => {
                const href = resolveHref(locale, cta?.URL ?? cta?.href ?? '');
                const target = cta?.newTab ? '_blank' : (cta?.target as any);

                return (
                  <Button
                    as={Link}
                    key={cta?.id ?? index}
                    href={href}
                    variant={(cta?.variant as any) ?? 'primary'}
                    target={target}
                    rel={target === '_blank' ? 'noreferrer noopener' : undefined}
                    className="w-full py-3 sm:w-auto"
                  >
                    {cta?.text ?? ''}
                  </Button>
                );
              })}
            </div>
          ) : null}
        </div>

        {/* RIGHT: cover (csak ha van) */}
        {coverSrc ? (
          <motion.div
            initial={reduce ? false : { opacity: 0, y: 12 }}
            whileInView={reduce ? undefined : { opacity: 1, y: 0 }}
            viewport={{ once: true, amount: 0.35 }}
            transition={{ duration: 0.55, ease: [0.22, 1, 0.36, 1] }}
            className="relative w-full md:w-[420px] lg:w-[520px]"
          >
            <div className="relative aspect-[4/3] w-full overflow-hidden rounded-2xl bg-charcoal">
              <StrapiImage
                src={coverSrc}
                alt={coverAlt}
                fill
                className="object-cover"
                sizes="(min-width: 1024px) 520px, (min-width: 768px) 420px, 100vw"
              />

              {/* finom prémium overlay */}
              <div className="pointer-events-none absolute inset-0 bg-gradient-to-t from-lightblack/60 via-lightblack/10 to-transparent" />
              <div className="pointer-events-none absolute inset-0 ring-1 ring-lightblack/10" />
            </div>
          </motion.div>
        ) : null}
      </Container>
    </section>
  );
};
